import { Link, LinkMode } from '@zepdev/design-system-component-library-react';
import { clsx } from 'clsx';
import { Button } from '../zps-button/ZpsButton';
import { ZpsButtonVariant } from '../zps-button/zps-button.interface';
import { HeaderLongProps } from './headerlong.interface';

export const HeaderLong = ({
  textColor,
  type,
  tagline,
  headline,
  description,
  linkHref,
  linkText,
  buttonOnClick,
  buttonText,
}: HeaderLongProps) => {
  const containerClass =
    textColor === 'white'
      ? 'zep-text-typography-light-100'
      : textColor === 'black'
      ? 'zep-text-typography-dark-100'
      : 'zep-text-indigo-500';

  const linkClass =
    textColor === 'white'
      ? 'zep-text-yellow-500'
      : textColor === 'black'
      ? 'zep-text-typography-dark-100'
      : 'zep-text-indigo-500';

  const taglineClass =
    textColor === 'white'
      ? 'zep-text-steel-500'
      : textColor === 'black'
      ? 'zep-text-typography-dark-70/70'
      : 'zep-text-steel-500';

  return (
    <div
      className={clsx('zep-px-1.5', 'sm:zep-px-1', 'zep-break-words', 'zep-max-w-[1920px]', 'zep-mx-auto')}
      data-testid="header-long"
    >
      {/* Tagline wrapper */}
      <div className="zep-block zep-max-w-[944px] zep-self-center">
        <h3 className={clsx(taglineClass, 'zep-mb-0.5 zep-typography-taglineMD')}>{tagline}</h3>
      </div>

      {/* headline and Button/Link wrapper */}
      <div className="md:zep-flex md:zep-justify-between">
        <h2
          className={clsx(
            containerClass,
            'zep-mb-1.5 md:zep-mb-2.5 sm:zep-mb-1.5 md:zep-mr-2 zep-max-w-[944px] zep-typography-headlineLG-fluid-cqi',
          )}
        >
          {headline}
        </h2>
        {/* Button or Link or None */}
        <div className="md:zep-float-right">
          {type === 'button' && buttonText && buttonOnClick ? (
            <Button
              className="zep-mb-2.5 zep-whitespace-nowrap md:zep-mb-[0px]"
              label={buttonText}
              onClick={buttonOnClick}
              variant={ZpsButtonVariant.Primary}
            />
          ) : type === 'link' && linkHref && linkText ? (
            <Link
              className={clsx(
                linkClass,
                'zep-font-400',
                'zep-mb-2',
                'zep-whitespace-nowrap',
                'md:zep-mb-[0px]',
                'hover:zep-underline',
                'hover:zep-font-500',
                'zep-cursor-pointer',
              )}
              as={'div'}
              label={linkText}
              hasIcon={true}
              href={linkHref}
              mode={LinkMode.Standalone}
            />
          ) : null}
        </div>
      </div>

      {/* Description */}
      <div className="zep-max-w-[944px] zep-break-all">
        <p className={clsx(containerClass)}>{description}</p>
      </div>
    </div>
  );
};
